:root {
  --color-dark-text: #494450;
  --color-text: #615c69;
  --color-light-text: #d2bea6;
  --font-bebas: 'Bebas Neue', Arial, Helvetica, sans-serif;
  --bg-body: #f1e2cf;
  --bg-light: #e8d7c3;
  --bg-dark: #494450;
  --transition: 0.2s ease-in-out;
}

.cource-wrap-link {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 300px));
  justify-content: center;
  justify-items: center;
  gap: 20px;
  margin-bottom: 30px;
}

.cource-wrap-link h2 {
  grid-column-start: 1;
  grid-column-end: -1;
}

.cource-link {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  max-width: 300px;
  min-height: 80px;
  padding: 10px 60px;
  overflow: hidden;
  font-weight: 900;
  font-size: 20px;
  line-height: 22px;
  text-align: center;
  color: var(--color-dark-text);
  text-decoration: none;
  background: var(--bg-body);
  border-radius: 20px;
  box-shadow: 0 6px 18px var(--color-light-text);
  transition: background var(--transition);
}

.cource-link svg {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: auto;
  height: 100%;
  opacity: 0;
  transition: opacity var(--transition), fill var(--transition);
}

.cource-link:hover svg {
  opacity: 0.1;
  fill: var(--color-dark-text);
}

.cource-link:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
}

.cource-link:focus-visible svg {
  opacity: 0.05;
}

.cource-link:active,
.cource-link--active {
  color: var(--color-light-text);
  background-color: var(--bg-dark);
}

.cource-link:active svg,
.cource-link--active svg {
  opacity: 0.1;
  fill: var(--color-light-text) !important;
}

.cource-level {
  width: 100%;
  max-width: 340px;
  border-radius: 20px;
  box-shadow: 0px 6px 18px 0px var(--color-light-text);
}

.cource-level__top {
  box-sizing: border-box;
  padding: 18px 16px 16px;
  text-align: center;
  background-color: var(--color-light-text);
  border-radius: 20px 20px 0 0;
}

.cource-level__title {
  margin: 0 0 5px;
  font-weight: 900;
  font-size: 20px;
  line-height: 22px;
  color: var(--color-dark-text);
  text-transform: uppercase;
}

.cource-level__bottom {
  position: relative;
  z-index: 2;
  box-sizing: border-box;
  padding: 0 20px 10px;
  text-align: center;
}

.cource-level__bottom .btn {
  width: 100%;
  margin-bottom: 10px;
  font-size: 26px;
  letter-spacing: 2px;
}

.cource-level__indicator {
  display: inline-grid;
  grid-template-columns: repeat(3, 24px);
  gap: 4px;
}

.cource-level__indicator span {
  height: 10px;
  border: 1px solid #fff;
}

.cource-level__indicator--1 span:first-child {
  background-image: linear-gradient(to right, #fff 50%, transparent 50%);
}

.cource-level__indicator--2 span:first-child {
  background-color: #fff;
}

.cource-level__indicator--3 span:first-child {
  background-color: #fff;
}

.cource-level__indicator--3 span:nth-child(2) {
  background-image: linear-gradient(to right, #fff 50%, transparent 50%);
}

.cource-level__indicator--4 span:first-child {
  background-color: #fff;
}

.cource-level__indicator--4 span:nth-child(2) {
  background-color: #fff;
}

.cource-level__indicator--5 span:first-child {
  background-color: #fff;
}

.cource-level__indicator--5 span:nth-child(2) {
  background-color: #fff;
}

.cource-level__indicator--5 span:last-child {
  background-image: linear-gradient(to right, #fff 50%, transparent 50%);
}

.cource-level__indicator--6 span {
  background-color: #fff;
}

.cource-level__icon {
  margin-top: 5px;
  padding-bottom: 68.825%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto 100%;
}

.cource-level__icon--ass {
  background-image: url(../img/cource-level/ass/1.png);
}

.page-footer {
  --color-footer-text: #85818c;
  font-size: 12px;
  line-height: 16px;
  color: var(--color-footer-text);
  background-color: var(--bg-dark);
  flex-grow: 0!important;
}

.page-footer a {
  color: inherit;
}

.blank {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 520px;
  padding-top: 50px;
  padding-bottom: 50px;
}

.form {
  width: 100%;
}

@media (min-width: 576px) {
  .cource-level__bottom .btn {
    min-width: 240px;
  }
}

@media (min-width: 768px) {
  .cource-link {
    min-height: 100px;
    padding: 15px 40px;
  }

  .page-footer__col {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }

  .page-footer__page-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .page-footer__social-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  .page-footer__social-item:not(:last-child) {
    margin-right: 15px;
  }

  .page-footer__social-link {
    display: flex;
    align-items: center;
    width: 20px;
    height: 20px;
  }

  .page-footer__social-link:focus-visible {
    outline-width: 2px;
    outline-style: double;
    outline-color: var(--color-footer-text);
    outline-offset: 2px;
  }

  .page-footer__social-link svg {
    fill: var(--color-footer-text);
  }

  .page-footer__social-link--instagram:hover svg {
    fill: url(#a);
  }

  .page-footer__social-link--vk svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--vk:hover svg {
    fill: #2787f5;
  }

  .page-footer__social-link--vk:hover svg .center {
    fill: #fff;
  }

  .page-footer__social-link--youtube svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--youtube:hover svg {
    fill: #ff0000;
  }

  .page-footer__social-link--youtube:hover svg .center {
    fill: #fff;
  }

  .page-footer__social-link--telegram svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--telegram:hover svg {
    fill: url(#a1);
  }

  .page-footer__social-link--telegram:hover svg .center {
    fill: #fff;
  }

  .page-footer__social-link--facebook svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--facebook:hover svg {
    fill: #1877f2;
  }

  .page-footer__social-link--facebook:hover svg .center {
    fill: #fff;
  }
}

@media (min-width: 990px) {
  .cource-level__bottom {
    overflow: hidden;
    height: 75px;
    transition: height var(--transition);
  }

  .cource-level__bottom .btn {
    max-width: 280px;
    transition: opacity var(--transition), box-shadow var(--transition);
  }

  .cource-level__bottom .btn:last-child {
    opacity: 0;
  }

  .cource-level {
    height: 390px;
  }

  .cource-level:hover,
  .cource-level:focus,
  .cource-level:focus-within {
    outline: none;
  }

  .cource-level:hover .cource-level__icon,
  .cource-level:focus .cource-level__icon,
  .cource-level:focus-within .cource-level__icon {
    filter: grayscale(0%);
  }

  .cource-level:hover .cource-level__bottom,
  .cource-level:focus .cource-level__bottom,
  .cource-level:focus-within .cource-level__bottom {
    position: relative;
    z-index: 1;
    height: 140px;
    background-color: var(--bg-body);
    border-radius: 0 0 20px 20px;
    box-shadow: 0 16px 18px 0px var(--color-light-text);
  }

  .cource-level:hover .cource-level__bottom .btn:last-child,
  .cource-level:focus .cource-level__bottom .btn:last-child,
  .cource-level:focus-within .cource-level__bottom .btn:last-child {
    opacity: 1;
  }

  .cource-level__icon {
    filter: grayscale(90%);
    transition: filter var(--transition);
  }
}

@media (max-width: 767px) {
  .page-footer__col {
    padding: 0;
  }

  .page-footer__nav-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  .page-footer__nav-item {
    position: relative;
    flex-grow: 1;
    box-sizing: border-box;
  }

  .page-footer__nav-item svg {
    display: block;
    margin: 13px auto 10px;
    fill: var(--color-text);
  }

  .page-footer__nav-item:not(:last-child) {
    border-right: 1px solid #504b58;
  }

  .page-footer__nav-link {
    display: block;
    padding: 10px 5px 8px;
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
    font-family: var(--font-bebas);
    text-align: center;
    color: var(--color-light-text);
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 2px;
    transition: color var(--transition);
  }

  .page-footer__nav-link.active {
    color: var(--color-text);
  }

  .page-footer__nav-link:hover {
    color: var(--color-text);
  }

  .page-footer__nav-link:focus-visible {
    outline-width: 1px;
    outline-style: double;
    outline-color: #d1bda5;
    outline-offset: -2px;
  }

  .page-footer__nav-link.active::after {
    content: url(../img/icon/page-footer__nav-link-active.svg);
    position: absolute;
    top: -13px;
    left: 50%;
    transform: translateX(-50%);
  }
}

@media (max-width: 700px) {
  .cource-wrap-link {
    flex-direction: column;
    align-items: center;
  }
}